<template>
  <div class="col-4">
    <div class="panel">
      <div class="panel-heading">
        <div class="panel-title">案件数量走势</div>
      </div>
      <div class="panel-body" v-loading="loading" style="overflow: auto;">
        <div class="chart" id="chart4"></div>
      </div>
    </div>
  </div>
</template>

<script>
import watchDate from '@/mixins/watchDate'
export default {
  props: ['dateType'],
  data () {
    return {
      loading: false
    }
  },
  mixins: [
    watchDate
  ],
  mounted () {
    this.init()
  },
  methods: {
    init () {
      var self = this
      this.loading = true
      this.$http.get({
        api: 'A_S_CASE_TREND',
        params: {
          dateType: this.dateType
        },
        loading: this.$data,
        callback: (res) => {
          self.loading = false
          self.draw(res)
        }
      })
    },
    draw (a) {
      let b = this.dataFormat(a)
      let option = {
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: b.legend
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: b.xAxis
        },
        yAxis: {
          type: 'value'
        },
        series: b.series
      }
      let myChart = this.$echarts.init(document.getElementById('chart4'), 'macarons')
      myChart.setOption(option)
    },
    dataFormat (a) {
      let obj = {
        series: [],
        xAxis: [],
        legend: []
      }
      obj.legend = a.category
      let aseri = []
      a.series.map(o => {
        obj.xAxis.push(o.name)
        o.data.map((o2, i) => {
          if (!aseri[i]) aseri[i] = []
          aseri[i].push(o2)
        })
      })
      aseri.map((o, i) => {
        if (!obj.series[i]) obj.series[i] = {}
        obj.series[i] = {
          name: a.category[i],
          data: o,
          type: 'line'
        }
      })
      return obj
    }
  }
}

</script>



